import Swiper, { Navigation, Keyboard } from 'swiper';

Swiper.use([Navigation, Keyboard]);

const swiper = new Swiper('.ourAssistance__sliders', {
  slidesPerView: 1,
  spaceBetween: 50,
  grabCursor: true,
  speed: 500,

  keyboard: {
    enabled: true,
    onlyInViewport: true,
    pageUpDown: true,
  },

  navigation: {
    nextEl: '.ourAssistance__slider-btn--next',
    prevEl: '.ourAssistance__slider-btn--prev',
  },

  breakpoints: {
    576: {
      slidesPerView: 1.5,
    },

    768: {
      slidesPerView: 2,
    },

    993: {
      slidesPerView: 2.5,
    },

    1025: {
      slidesPerView: 3,
    },
  },
});
